  color: #1b1c21;
  font-size: 14px;
  margin-bottom: 16px; }
  @media (min-width: 768px) {
    .repair-pricelist__text {
      margin-bottom: 0; }
      .repair-pricelist__text:nth-child(2) {
        flex: 2; }
      .repair-pricelist__text:nth-child(6), .repair-pricelist__text:nth-child(4) {
        flex: 1; } }
  .repair-pricelist__text--title {
    color: #7E7E82;
    font-size: 16px; }
    @media (min-width: 768px) {
      .repair-pricelist__text--title {
        display: none; } }

.repair-pricelist__category-button {
  position: absolute;
  right: 16px;
  bottom: 16px;
  width: 120px; }
  @media (min-width: 768px) {
    .repair-pricelist__category-button {
      position: static; } }

.repair-pricelist__footer {
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 24px;
  padding: 0 16px;
  padding-bottom: 40px; }
  @media (min-width: 768px) {
    .repair-pricelist__footer {
      width: 420px;
      padding: 0 24px;
      padding-bottom: 47px; } }
  @media (min-width: 1366px) {
    .repair-pricelist__footer {
      padding: 0 32px;
      padding-bottom: 50px; } }

.repair-pricelist__footer-item {
  font-size: 14px; }

.repair-pricelist__footer-button {
  position: relative;
  font-size: 16px;
  color: #1B1C21;
  font-family: "TT Lakes"; }
  .repair-pricelist__footer-button::before {
    position: absolute;
    right: -20px;
    content: "";
    width: 24px;
    height: 24px;
    background-image: url(./static/comOfferArrow.svg); }
